<template>
  <div id="login">
    <div class="login text-center">
      <div v-if="!user" class="login">
        <router-link to="/login" class="text-color1"><i class="fas fa-air-freshener"></i>&nbsp;</router-link>
      </div>
      <div v-if="user" class="logout">
        <span class="name">{{user.name}}</span>
        <br>
        <router-link to="/cart" class="txtCart">
          <i class="fas fa-shopping-cart"></i> 
        </router-link>
        <!-- sessionStorage user  ,  . -->
        <div class="logout btn" @click="logout"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user: null
    };
  },
  methods: {
    logout() {
      sessionStorage.removeItem("user");
      location.reload();
    }
  },
  created() {
    // data methods       .
    // sessionStorage user  json  .
    this.user = JSON.parse(sessionStorage.getItem("user"));
  }
};
</script>

<style>
.login,

.login a {
  margin-top: 40px;
  margin-bottom: 30px;
  font-size: 1.5rem;
}

a:hover {
  cursor: pointer;
  text-decoration: none;
  color: #014014;
}
.name {
  color: #3d8e64;
  font-size: 1.5 rem;
}

.logout {
}
.logout .btn {
  color: black;
}

.logout a {
  color: #f20b04;
  font-size: 1.2rem;
}

.logout .btn:hover {
  cursor: pointer;
  color: black;
}

</style>



